@import "../../global";

.ProductItem {
  width: 240px;
  & img {
    width: 240px;
    height: 240px;
    border-radius: 20px;
    object-fit: cover;
  }

  .product-info {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top: 12px;

    & div p:nth-child(1) {
      font-weight: bold;
      font-size: $font-md;
      margin-top: 0;
      margin-bottom: 4px;
    }
    & div p:nth-child(2) {
      font-size: $font-sm;
      margin-top: 0;
      margin-bottom: 0;
      color: $color-secundary;
    }
    & figure {
      margin: 0;
      & img {
        width: 35px;
        height: 35px;
        cursor: pointer;
      }
    }
  }
}

@media (max-width: 640px) {
  .cards-container {
    grid-template-columns: repeat(auto-fill, 140px);
  }
  .ProductItem {
    width: 140px;
  }
  .ProductItem img {
    width: 140px;
    height: 140px;
  }
}
